"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { ClientHeader } from "@/components/client/client-header"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Label } from "@/components/ui/label"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Textarea } from "@/components/ui/textarea"
import { Separator } from "@/components/ui/separator"
import { useCartStore, useAuthStore, useOrderStore } from "@/lib/store"
import { mockMarket, mockProducts } from "@/lib/mock-data"
import { formatCurrency } from "@/lib/currency"
import { useToast } from "@/hooks/use-toast"
import type { Order } from "@/lib/types"
import { CreditCard, Smartphone, DollarSign, MapPin, StickyNote } from "lucide-react"

export default function CheckoutPage() {
  const router = useRouter()
  const { toast } = useToast()
  const { items, getTotal, clearCart } = useCartStore()
  const { user, isAuthenticated } = useAuthStore()
  const { addOrder } = useOrderStore()

  const [paymentMethod, setPaymentMethod] = useState<Order["paymentMethod"]>("pix")
  const [notes, setNotes] = useState("")
  const [isProcessing, setIsProcessing] = useState(false)

  useEffect(() => {
    if (!isAuthenticated) {
      router.push("/login")
    }
  }, [isAuthenticated, router])

  const subtotal = getTotal()
  const deliveryFee = mockMarket.deliveryFee
  const total = subtotal + deliveryFee

  const handlePlaceOrder = async () => {
    if (!user?.addresses[0]) {
      toast({
        title: "Endere√ßo n√£o encontrado",
        description: "Adicione um endere√ßo para continuar",
        variant: "destructive",
      })
      return
    }

    setIsProcessing(true)

    // Simulate processing
    await new Promise((resolve) => setTimeout(resolve, 1500))

    const orderItems = items.map((item) => {
      const product = mockProducts.find((p) => p.id === item.productId)!
      return {
        productId: item.productId,
        productName: product.name,
        productImage: product.image,
        quantity: item.quantity,
        price: item.price,
        subtotal: item.price * item.quantity,
      }
    })

    addOrder({
      userId: user.id,
      items: orderItems,
      subtotal,
      deliveryFee,
      totalAmount: total,
      status: "pending",
      paymentMethod,
      deliveryAddress: user.addresses[0],
      notes: notes || undefined,
      estimatedDelivery: mockMarket.estimatedDeliveryTime,
    })

    clearCart()

    toast({
      title: "Pedido realizado!",
      description: "Seu pedido foi confirmado e est√° sendo preparado",
    })

    router.push("/client/orders")
  }

  if (!isAuthenticated) {
    return null
  }

  const paymentMethods = [
    { id: "pix", label: "PIX", icon: Smartphone },
    { id: "credit", label: "Cart√£o de Cr√©dito", icon: CreditCard },
    { id: "debit", label: "Cart√£o de D√©bito", icon: CreditCard },
    { id: "cash", label: "Dinheiro", icon: DollarSign },
  ]

  return (
    <div className="min-h-screen bg-background pb-32">
      <ClientHeader showBack />
      <main className="space-y-4 px-4 py-6">
        <h1 className="text-2xl font-bold">Finalizar pedido</h1>

        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2 text-base">
              <MapPin className="h-5 w-5 text-primary" />
              Endere√ßo de entrega
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-sm">
              <p className="font-medium">
                {user?.addresses[0]?.street}, {user?.addresses[0]?.number}
              </p>
              {user?.addresses[0]?.complement && (
                <p className="text-muted-foreground">{user.addresses[0].complement}</p>
              )}
              <p className="text-muted-foreground">
                {user?.addresses[0]?.neighborhood} - {user?.addresses[0]?.city}, {user?.addresses[0]?.state}
              </p>
              <p className="text-muted-foreground">CEP: {user?.addresses[0]?.zipCode}</p>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2 text-base">
              <CreditCard className="h-5 w-5 text-primary" />
              Forma de pagamento
            </CardTitle>
          </CardHeader>
          <CardContent>
            <RadioGroup
              value={paymentMethod}
              onValueChange={(value) => setPaymentMethod(value as Order["paymentMethod"])}
            >
              <div className="space-y-3">
                {paymentMethods.map((method) => {
                  const Icon = method.icon
                  return (
                    <div key={method.id} className="flex items-center space-x-2">
                      <RadioGroupItem value={method.id} id={method.id} />
                      <Label htmlFor={method.id} className="flex flex-1 cursor-pointer items-center gap-2">
                        <Icon className="h-4 w-4" />
                        {method.label}
                      </Label>
                    </div>
                  )
                })}
              </div>
            </RadioGroup>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2 text-base">
              <StickyNote className="h-5 w-5 text-primary" />
              Observa√ß√µes
            </CardTitle>
          </CardHeader>
          <CardContent>
            <Textarea
              placeholder="Adicione observa√ß√µes para o entregador (opcional)"
              value={notes}
              onChange={(e) => setNotes(e.target.value)}
              rows={3}
            />
          </CardContent>
        </Card>

        <Card>
          <CardContent className="space-y-3 p-4">
            <div className="flex items-center justify-between text-sm">
              <span className="text-muted-foreground">Subtotal</span>
              <span className="font-medium">{formatCurrency(subtotal)}</span>
            </div>
            <div className="flex items-center justify-between text-sm">
              <span className="text-muted-foreground">Taxa de entrega</span>
              <span className="font-medium">{formatCurrency(deliveryFee)}</span>
            </div>
            <Separator />
            <div className="flex items-center justify-between">
              <span className="font-semibold">Total</span>
              <span className="text-xl font-bold text-primary">{formatCurrency(total)}</span>
            </div>
          </CardContent>
        </Card>
      </main>

      <div className="fixed bottom-0 left-0 right-0 border-t bg-background p-4">
        <Button className="w-full" size="lg" onClick={handlePlaceOrder} disabled={isProcessing}>
          {isProcessing ? "Processando..." : `Confirmar pedido ¬∑ ${formatCurrency(total)}`}
        </Button>
      </div>
    </div>
  )
}
